<template>
  <div class="flex gap-10 container xl:max-w-screen-lg mx-auto p-6 flex-col-reverse lg:flex-row">
    <aside class="w-full flex-1 lg:w-1/4">
      <div class="space-y-4 top-6 sticky">
        <header class="w-full border-b border-gray-100">
          <div class="table border-b border-teal-600 text-teal-600 pb-3">
            <p class="font-medium rtl:font-semibold text-sm">Settings</p>
          </div>
        </header>
        <div
          class="table mx-auto"
          :class="{
            'text-red-500': !color,
          }"
          :style="`color: ${color}`"
        >
          <spinner :size="size" :width="width" />
        </div>
        <form class="w-full space-y-4">
          <div class="form-group">
            <div class="with-label">
              <div class="w-full flex gap-3 items-stretch">
                <input type="color" name="color" id="color" placeholder="#000000" v-model="color" class="!p-1 !h-12 !w-12" />
                <input type="text" name="text" id="text" placeholder="#000000" v-model="color" class="flex-1 !h-12" />
              </div>
              <label for="color">Color</label>
            </div>
          </div>
          <div class="form-group">
            <div class="with-label">
              <input type="number" name="width" min="1" id="width" placeholder="2" v-model="width" />
              <label for="width">Width</label>
            </div>
          </div>
          <div class="actions-group !gap-4 justify-between">
            <div class="form-group-with-check" v-for="(item, index) in sizes" :key="index">
              <input type="radio" name="sizesGroup" :value="item" :id="item" v-model="size" />
              <label :for="item" class="capitalize">{{ item }}</label>
            </div>
          </div>
          <div class="form-group" v-if="size === 'custom'">
            <div class="with-label">
              <div class="w-full flex gap-3 items-stretch">
                <input type="number" name="custom_height" id="custom_height" :placeholder="custom_height" v-model="custom_height" class="flex-1" />
                <input type="number" name="custom_width" id="custom_width" :placeholder="custom_width" v-model="custom_width" class="flex-1" />
              </div>
              <label for="color">Custom size (px)</label>
            </div>
            <p class="text-gray-400 font-medium rtl:font-semibold text-sm italic mt-2">This does not mean that you will rely on that name '.custom', but you can change the name as you want and add the appropriate properties to display it</p>
          </div>
        </form>
        <component :is="`style`">
          {{ style }}
        </component>
      </div>
    </aside>
    <main class="flex-1 w-full lg:w-3/4">
      <article class="max-w-full lg:max-w-prose prose prose-code:!p-0 prose-code:font-fira w-full hover:prose-a:underline prose-a:no-underline prose-p:!my-2" v-html="content" data-content="content"></article>
    </main>
  </div>
</template>

<script>
  import VueCustomSpinner from 'vue-custom-spinner';
  export default {
    name: 'HomePage',
    data() {
      return {
        markdown: '# hello world',
        size: 'lg',
        width: 2,
        color: '#000000',
        sizes: ['sm', 'md', 'lg', 'xl', 'custom'],
        custom_height: 120,
        custom_width: 120,
        content: null,
      };
    },
    created() {
      this.content = require(`!!html-loader!markdown-loader!@/assets/articles/installation.md`).default;
    },
    computed: {
      style() {
        return `
            .custom {
              width: ${this.custom_width}px;
              height: ${this.custom_height}px;
            }
        `;
      },
    },
    methods: {},
    components: { spinner: VueCustomSpinner },
  };
</script>

<style lang="scss">
  @import 'highlight.js/scss/night-owl.scss';
  [data-anchor]:not([data-anchor='']) {
    &:hover {
      .anchor {
        @apply relative no-underline hover:no-underline;
        &:before {
          content: '#';
          @apply relative;
        }
      }
    }
  }
</style>
